import md5 from '../../static/util/md5.js';
import mInput from '@/components/m-input.vue';
import img from '../../static/img/login/Login-bg.png';
import service from '../../static/service/service.js';

export default {
	components: {
		mInput
	},
	data() {
		return {
			// 账号
			account: '',
			// 密码
			password: '',
			// 为了适配小程序 将顶部背景图转成base64
			background: img,
			// 用户头像
			wechatIcon: '',
			// wechatCode
			wechatCode: ''
		}
	},
	methods: {
		wxGetUserInfo: function(res) {
			console.log("123");
			let _this = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					_this.wechatCode = loginRes.code; 
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							_this.wechatIcon = infoRes.userInfo.avatarUrl;
						}
					});
				},
			});
			_this.updateUserInfo()
		},
		async updateUserInfo() {
			console.log(this.account)
			console.log(this.account.length)
			// 名称判断
			if (this.account.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的名称'
				});
				return;
			}

			// 密码判断
			if (this.password.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的密码'
				});
				return;
			}

			const res = await service.login(this.account, md5(this.password), this.wechatIcon, this.wechatCode)
			if (res.errno == 0) {
				// 名称
				uni.setStorageSync('user_name', res.data.name)
				// 头像
				uni.setStorageSync('user_icon', res.data.photo)
				// 分组
				uni.setStorageSync('user_group', res.data.bzmc)
				// token
				uni.setStorageSync('user_token', res.data.token)
				uni.showToast({
					icon: 'none',
					title: '登录成功'
				});
				setTimeout(function() {
					uni.hideToast(),
						uni.reLaunch({
							url: '../task/task'
						})
				}, 1500);
			} else {
				uni.showToast({
					icon: 'none',
					title: res.errmsg
				})
			}
		}
	}
}
